import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, reset } from "redux-form";
import { hideModal, showModal } from "../../../actions/datamaster_action";
import { ReanderField } from "../../../components/helpers/field";
import ModalGlobal from "../../../components/helpers/ModalGlobal";
import { ToastNotification } from "../../../components/helpers/notification";
import { Panel } from "../../../components/panel/panel";
import { postData } from "../../../config/axios";
import AmbilPoin from "./AmbilPoin/AmbilPoin";
import TambahPoin from "./TambahPoin/TambahPoin";
// import { getData } from "../../../config/axios";
import Loading from 'react-fullscreen-loading';

const maptostate = (state) => {
  return {
    datakondisi: state.datamaster.getDataKondisiBarang,
    setHancurBarang: state.databarang.setHancurBarang,
  };
};
class HeadFormPoint extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      kondisi_barang: "kosong",
      isLoadingTambahPoin: false,
      isLoadingAmbilPoin: false,
      FormModal: "",
    };
  }

  simpanTambahPoint(data) {
    this.setState({
      isLoadingTambahPoin: true,
    });
    let data_member = JSON.parse(localStorage.getItem("data_member") || []);
    let data_post = {
      kode_member: data_member[0].kode_member,
      nama_customer: data_member[0].nama_customer,
      alamat_customer: data_member[0].alamat_customer,
      poin: parseInt(data.jumlah_poin),
    };
    postData("trxmember/poin/tambah", data_post)
      .then((res) => {
        ToastNotification("success", res.data.message);
        localStorage.setItem("data_member", JSON.stringify([]));
        this.props.dispatch(reset("HeadFormPoint"));
        document.getElementById("transaksi_poin_nama_member").value = "";
        document.getElementById("transaksi_poin_alamat").value = "";
        document.getElementById("transaksi_poin_no_hp").value = "";
        this.setState({
          isLoadingTambahPoin: false,
        });
        this.props.dispatch(hideModal());
      })
      .catch((err) => {
        this.setState({
          isLoadingTambahPoin: false,
        });
        ToastNotification("info", "Terjadi Kesalahan Saat Mengirim Data");
      });
  }
  simpanAmbilPoint(data) {
    console.log(data);
    this.setState({
      isLoadingAmbilPoin: true,
    });
    let data_member = JSON.parse(localStorage.getItem("data_member") || []);
    let data_post = {
      kode_member: data_member[0].kode_member,
      nama_customer: data_member[0].nama_customer,
      alamat_customer: data_member[0].alamat_customer,
      poin: parseInt(data.jumlah_poin),
    };
    postData("trxmember/poin/ambil", data_post)
      .then((res) => {
        ToastNotification("success", res.data.message);
        localStorage.setItem("data_member", JSON.stringify([]));
        this.props.dispatch(reset("HeadFormPoint"));
        document.getElementById("transaksi_poin_nama_member").value = "";
        document.getElementById("transaksi_poin_alamat").value = "";
        document.getElementById("transaksi_poin_no_hp").value = "";
        this.setState({
          isLoadingAmbilPoin: false,
        });
        this.props.dispatch(hideModal());
      })
      .catch((err) => {
        this.setState({
          isLoadingAmbilPoin: false,
        });
        ToastNotification("info", "Terjadi Kesalahan Saat Mengirim Data");
      });
  }
  ShowModalTambahPoin() {
    let data_member = localStorage.getItem("data_member") || [];
    if (data_member.length === 0) {
      ToastNotification("info", "Kode Member Belum Diisi");
    } else {
      this.props.dispatch(showModal());
      this.setState({
        FormModal: "ShowModalTambahPoin",
      });
    }
  }
  ShowModalAmbilPoin() {
    let data_member = localStorage.getItem("data_member") || [];
    if (data_member.length === 0) {
      ToastNotification("info", "Kode Member Belum Diisi");
    } else {
      this.props.dispatch(showModal());
      this.setState({
        FormModal: "ShowModalAmbilPoin",
      });
    }
  }
  render() {
    return (
      <Panel>
        <div className="row">
          <div className="col-8">
            <div className="row">
              <div className="col-lg-6">
                <Field
                  onChange={this.props.getKodeMember}
                  name="kode_member"
                  component={ReanderField}
                  type="text"
                  label="Kode Member"
                  placeholder="Masukan Kode Member"
                />
              </div>
              <div className="col-lg-6">
                <Field
                  id="transaksi_poin_nama_member"
                  name="nama_member"
                  readOnly={true}
                  component={ReanderField}
                  type="text"
                  label="Nama Member"
                  placeholder="Masukan Nama Member"
                />
              </div>
            </div>
          </div>
          <div className="col-4 text-center">
            <> &nbsp; </>
            <h1
              style={
                this.props.point === "POINT"
                  ? {
                      fontSize: "4.875rem",
                      position: "absolute",
                      left: "56px",
                      top: "25px",
                    }
                  : {
                      fontSize: "4.875rem",
                      position: "absolute",
                      left: "110px",
                      top: "25px",
                    }
              }
            >
              {" "}
              {this.props.point}{" "}
            </h1>
          </div>
          <div className="col-8">
            <div className="row">
              <div className="col-lg-6">
                <Field
                  id="transaksi_poin_alamat"
                  name="alamat"
                  readOnly={true}
                  component={ReanderField}
                  type="text"
                  label="Alamat"
                  placeholder="Masukan Alamat"
                />
              </div>
              <div className="col-lg-6">
                <Field
                  id="transaksi_poin_no_hp"
                  name="no_hp"
                  readOnly={true}
                  component={ReanderField}
                  type="text"
                  label="No Hp"
                  placeholder="Masukan No Hp"
                />
              </div>
            </div>
          </div>
          <div className="col-4 text-center">
            {/* <Field
              name="point"
              readOnly={true}
              component={ReanderField}
              type="text"
              placeholder="Point Saya"
            /> */}
          </div>

          {/* <div className="col-lg-2"></div> */}
          <div className="col-lg-6">
            <label>&nbsp;</label>
            <button
              type="button"
              onClick={() => this.ShowModalTambahPoin()}
              className="btn btn-primary btn-block"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Tambah Poin
                </>
              ) : (
                "Tambah Poin"
              )}
            </button>
          </div>
          <div className="col-lg-6">
            <label>&nbsp;</label>
            <button
              type="button"
              onClick={() => this.ShowModalAmbilPoin()}
              className="btn btn-warning btn-block"
            >
              {this.props.isLoadingBatal ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Ambil Poin
                </>
              ) : (
                "Ambil Poin"
              )}
            </button>
          </div>
        </div>
        <ModalGlobal
          size="L"
          title={
            this.state.FormModal === "ShowModalTambahPoin"
              ? "Tambah Poin"
              : "Ambil Poin"
          }
          content={
            this.state.FormModal === "ShowModalTambahPoin" ? (
              <TambahPoin
                isLoadingTambahPoin={this.state.isLoadingTambahPoin}
                onSubmit={(data) => this.simpanTambahPoint(data)}
              />
            ) : (
              <AmbilPoin
                isLoadingAmbilPoin={this.state.isLoadingAmbilPoin}
                onSubmit={(data) => this.simpanAmbilPoint(data)}
              />
            )
          }
        />
         {this.state.isLoadingAmbilPoin || this.state.isLoadingTambahPoin  ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </Panel>
    );
  }
}

HeadFormPoint = reduxForm({
  form: "HeadFormPoint",
  enableReinitialize: true,
})(HeadFormPoint);
export default connect(maptostate, null)(HeadFormPoint);
