import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { ReanderField } from "../../../../components/helpers/field";
import Loading from 'react-fullscreen-loading';

class AmbilPoin extends Component {
  state = {};
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-6">
            <Field
              name="jumlah_poin"
              component={ReanderField}
              type="number"
              label="Jumlah Tambah Poin"
              placeholder="Masukan Jumlah Tambah Poin"
            />
          </div>
          <div className="col-6">
            <label htmlFor=""> &nbsp; </label>
            <button
              className="btn btn-primary btn-block"
              disabled={this.props.isLoadingAmbilPoin}
              type="submit"
            >
              {this.props.isLoadingAmbilPoin ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
        {this.props.isLoadingAmbilPoin ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

AmbilPoin = reduxForm({
  form: "AmbilPoin",
  enableReinitialize: true,
})(AmbilPoin);
export default connect()(AmbilPoin);
